import { Component, OnInit } from '@angular/core';
import { DepartmentService } from 'src/app/services/department.service';
import { ToastrService } from 'ngx-toastr';
import { Departamento } from 'src/app/models/department';
import {ViewChild, ElementRef} from '@angular/core';

@Component({
  selector: 'app-department-list',
  templateUrl: './department-list.component.html',
  styleUrls: ['./department-list.component.css']
})
export class DepartmentListComponent implements OnInit {

  constructor(private service: DepartmentService, 
    private toastr: ToastrService) { }

  @ViewChild('closeBtn') closeBtn: ElementRef;


  ngOnInit() {
    this.service.refreshList();
  }

  populateForm(dep: Departamento) {
    this.service.formData = Object.assign({}, dep);
  }

  onDelete(IdRegistro) {
    if (confirm('¿Está seguro que desea borrar este departamento?')) {
      this.service.deleteDepartment(IdRegistro)
        .subscribe(res => {
          this.service.refreshList();
          this.toastr.warning('Borrado completo', 'Departamentos');
        },
          err => {
            console.log(err);
          })
    }
  }

  private closeModal(): void {
    this.closeBtn.nativeElement.click();
}
  
}